<template>
  <div class="custom-edit-wrap">
    <el-row class="breadcrumb-style">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }">防护管理</el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/name-list' }">名单防护</el-breadcrumb-item>
        <el-breadcrumb-item v-if="uuid == 'new'">新增</el-breadcrumb-item>
        <el-breadcrumb-item v-else>编辑</el-breadcrumb-item>
      </el-breadcrumb>
    </el-row>
    <el-row class="container-style">
      <el-col :span="24" v-loading.fullscreen.lock="loadingPage">
        <el-form class="custom-edit-form name-and-dict" :model="sysNameListForm" :rules="rules" ref="sysNameListForm"
          label-width="180px">
          <div>
            <el-form-item label="名单名称" prop="name_list_name">
              <el-input v-model="sysNameListForm.name_list_name" placeholder="请输入字母开头，字母或数字组合，仅支持_-两种符号"> </el-input>
            </el-form-item>
            <el-form-item label="名单描述">
              <el-input v-model="sysNameListForm.name_list_detail"> </el-input>
            </el-form-item>
            <el-form-item label="过期时间（秒）">
              <el-radio v-model="sysNameListForm.name_list_expire" label="false">永久生效</el-radio>
              <el-radio v-model="sysNameListForm.name_list_expire" label="ture">自定义</el-radio>
            </el-form-item>
            <el-form-item v-if="sysNameListForm.name_list_expire == 'ture'" prop="name_list_expire_time">
              <el-input v-model="sysNameListForm.name_list_expire_time" placeholder="请输入大于0的数字">
              </el-input>
            </el-form-item>
            <el-form-item label="匹配参数">
              <div class="match-box" v-for="(item, index) in ruleSmallMatchs" :key="index">
                <div class="match-box-content">
                  <div class="match_key_cascader">
                    <el-cascader separator=":" v-model="item.rule_match_key_list" :options="optionsMatchKey"
                      :props="propsMatchKey" @change="onChangeRuleMatchs($event, item, bigIndex)" clearable>
                    </el-cascader>
                  </div>
                  <div v-show="item.showInput" class="match_key_input">
                    <el-input v-model="item.rule_match_key" clearable
                      @change="onChangeRuleInput($event, item, bigIndex)"></el-input>
                  </div>
                </div>

                <el-button @click.prevent="removeRuleMatchs(item, bigIndex)">删除</el-button>
              </div>
              <el-button @click="addRuleMatchs(bigIndex)" plain type="primary" class="button-new">新增</el-button>
            </el-form-item>

            <el-form-item label="执行动作" prop="name_list_action">
              <el-select v-model="sysNameListForm.name_list_action" placeholder="请选择" @change="onChangeRuleAction()">
                <el-option v-for="item in ruleAction" :key="item.value" :label="item.label" :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>

            <el-form-item v-if="sysNameListForm.name_list_action == 'bot_check'">
              <el-select v-model="sysNameListForm.action_value" placeholder="请选择">
                <el-option v-for="item in optionsBotCheck" :key="item.value" :label="item.label" :value="item.value">
                </el-option>
              </el-select>
              <p class="form-info-color">
                说明：标准模式无需人机交互
              </p>
            </el-form-item>
          </div>
        </el-form>
        <el-row type="flex" class="margin-border" justify="space-between">
          <el-col :span="12">
            <a class="el-button el-button--primary is-plain" :href="'/#/name-list'">返回</a>
          </el-col>
          <el-col :span="12" class="text-align-right">
            <el-button type="primary" @click="onClickWebRuleProSubmit('sysNameListForm')" :loading="loading">保存
            </el-button>
          </el-col>
        </el-row>
      </el-col>
    </el-row>
  </div>
</template>
<script>
import { mixin, JXAjax, validateRuleName,  validatePositiveInt } from "../assets/scripts/common";
import { useRoute } from 'vue-router'
export default {
  mixins: [mixin],
  data() {
    return {
      loading: false,
      loadingPage: false,
      uuid:"",
      sysNameListForm: { 
        name_list_name: "",
        name_list_detail: "",
        name_list_rule: "",
        name_list_action: "",
        name_list_expire: "false",
        name_list_expire_time: "",
        action_value: "",
      },
      type: 'edit',

      optionsMatchKey: [
        {
          value: 'http_args',
          label: 'http_args',
          children: [
            { value: 'path', label: 'path', leaf: true },
            { value: 'query_string', label: 'query_string', leaf: true },
            { value: 'method', label: 'method', leaf: true },
            { value: 'src_ip', label: 'src_ip', leaf: true },
            { value: 'raw_body', label: 'raw_body', leaf: true },
            { value: 'version', label: 'version', leaf: true },
            { value: 'scheme', label: 'scheme', leaf: true },
            { value: 'raw_header', label: 'raw_header', leaf: true },
          ]
        },
        {
          value: 'header_args',
          label: 'header_args',
          children: [
            { value: 'host', label: 'host', leaf: true },
            { value: 'cookie', label: 'cookie', leaf: true },
            { value: 'referer', label: 'referer', leaf: true },
            { value: 'user_agent', label: 'user_agent', leaf: true },
            { value: 'default', label: '自定义', leaf: true },
          ]
        },
        {
          value: 'cookie_args',
          label: 'cookie_args',
          children: [
            { value: 'default', label: '自定义', leaf: true }
          ]
        },
        {
          value: 'uri_args',
          label: 'uri_args',
          children: [
            { value: 'default', label: '自定义', leaf: true }
          ]
        },
        {
          value: 'post_args',
          label: 'post_args',
          children: [
            { value: 'default', label: '自定义', leaf: true }
          ]
        },
        {
          value: 'json_post_args',
          label: 'json_post_args',
          children: [
            { value: 'default', label: '自定义', leaf: true }
          ]
        },
      ],
      propsMatchKey: {
        expandTrigger: 'hover',
        lazy: true,
        lazyLoad(node, resolve) {
          
        },
      },

      ruleSmallMatchs: [{
        rule_match_key_list: [],
        rule_match_key: '',
        showInput: false
      }],
      
      ruleAction: [
        { value: "block", label: "阻断请求" },
        { value: "reject_response", label: "拒绝响应" },
        { value: "watch", label: "观察模式" },
        { value: "bot_check", label: "人机识别" },
        { value: "all_bypass", label: "安全防护全加白" },
        { value: "web_bypass", label: "Web安全防护加白" },
        { value: "flow_bypass", label: "流量安全防护加白" },
      ],
      optionsBotCheck: [
        { value: "standard", label: "标准" },
        { value: "slipper", label: "滑块" },
        { value: "image", label: "图片验证码" },
      ],

    };
  },
  computed: {
    rules() {
      return {
        name_list_name: [
          {
            required: true,
            message: '请输入规则名称',
            trigger: ["blur", "change"],
          },
          {
            validator: validateRuleName,
            trigger: ["blur", "change"],
          },
        ],
        name_list_expire_time: [
          {
            required: true,
            message: '请输入大于0的数字',
            trigger: ["blur", "change"],
          },
          {
            validator: validatePositiveInt,
            trigger: ["blur", "change"],
          },
        ],
        name_list_action: [
          {
            required: true,
            message: '请选择执行动作',
            trigger: 'change'
          },
        ], 
      }
    }
  },
  mounted() {
    var t = this;
    const route = useRoute()
    t.uuid = route.params.uuid;
    if (t.uuid == "new") { 
      t.type = "new";
      t.loadingPage = false
    } else {
      t.type = "edit"
      t.loadingPage = false
      t.getData();
    }
  },
  methods: {
    getData() {
      var t = this;
      var postUrl = "/waf/waf_get_name_list";
      var oData = { name_list_name: t.uuid };
      JXAjax(
        "post",
        postUrl,
        oData,
        function (response) {
          t.loadingPage = false;
          t.sysNameListForm = response.data.message;
          var _rule_matchs = JSON.parse(t.sysNameListForm.name_list_rule)
          var _match = [];
          var _default = [
              "query_string",
              "json_post",
              "post",
              "cookie",
              "waf_description",
              "ctx",
              "string"
          ]

          for (var j in _rule_matchs) {
            var item = _rule_matchs[j]
            var key = item.key
            var value = item.value
            var show = "false"
            if (_default.indexOf(key) > -1) {
              show = "true"
            }

            _match.push({
              "rule_match_key_list": [key, value],
              "rule_match_key": key + ':' + value,
              "showInput": show
            })
          }
          t.ruleSmallMatchs = _match;
        },
        function () {
          t.loadingPage = false;
        },
        "no-message"
      );
    },
    onClickWebRuleProSubmit(sysNameListForm) {
      var t = this;      
      var _match = [];
      if (t.ruleSmallMatchs.length == 0) {
        t.$message({
          message: '请选择匹配参数',
          type: "error",
        });
        return false;
      }
      if (t.ruleSmallMatchs[0].rule_match_key == "") {
        t.$message({
          message: '请选择匹配参数',
          type: "error",
        });
        return false;
      }

      for (var j in t.ruleSmallMatchs) {
        var item = t.ruleSmallMatchs[j]
        var _str = "";
        var _arr = [];
        var _key = "";
        var _value = "";

        if (item.rule_match_key) {
          _arr = item.rule_match_key.split(":");
        }
        if (_arr.length > 0) {
          _key = _arr[0];
          _value = item.rule_match_key.replace(new RegExp(_key + ":"), "");
          _str = '{"key":"' + _key + '" , "value":"' + _value + '"}'
        }
        _match.push(JSON.parse(_str))
      }

      if (t.sysNameListForm.name_list_expire == "false") { 
        t.sysNameListForm.name_list_expire_time = 0;
      }

      var postUrl = "/waf/waf_edit_name_list";
      if (t.type == 'new') {
        postUrl = "/waf/waf_create_name_list";
      }

      t.sysNameListForm.name_list_rule = JSON.stringify(_match);

      this.$refs[sysNameListForm].validate((valid) => {
        if (valid) {
          t.loading = true;
          JXAjax(
            "post",
            postUrl,
            t.sysNameListForm,
            function (response) {
              t.loading = false;
              window.location.href = '/#/name-list/'
            },
            function () {
              t.loading = false;
            }
          );
        }
      });
    },
    onChangeRuleAction() {
      var t = this;
      t.action_value = "";
    },
    removeRuleMatchs(item) {
      var index = this.ruleSmallMatchs.indexOf(item);
      if (index !== -1) {
        this.ruleSmallMatchs.splice(index, 1);
      }
    },
    addRuleMatchs(){
      this.ruleSmallMatchs.push({rule_match_key: '', showInput: false})
    },
    onChangeRuleMatchs(event, item, bigIndex) {
      if (event && event.length > 0) { 
        var index = this.ruleSmallMatchs.indexOf(item);
        if (event[1] == 'default') {
          this.ruleSmallMatchs[index].showInput = true;
          this.ruleSmallMatchs[index].rule_match_key = event[0] + ':';
        } else {
          this.ruleSmallMatchs[index].rule_match_key = event[0] + ':' + event[1];
        }
      }
      
    },
    onChangeRuleInput(event, item, bigIndex) {
      var index = this.ruleSmallMatchs.indexOf(item);
      if(event == '') {
        this.ruleSmallMatchs[index].showInput = false;
        this.ruleSmallMatchs[index].rule_match_key = "";
        this.ruleSmallMatchs[index].rule_match_key_list = [];
      } else {
        this.ruleSmallMatchs[index].rule_match_key = event;
      }
    },
  },
};
</script>
<style>
.custom-edit-wrap {
  max-width: 800px;
  min-width: 400px;
}
.custom-edit-wrap .match-inline-block {
  width: 192px;
}
.custom-edit-form .el-checkbox + .el-checkbox {
  margin-left: 0px;
  margin-right: 30px;
}
.custom-edit-form .el-checkbox {
  margin-left: 0px;
  margin-right: 30px;
}
.custom-edit-form .el-select {
  width: 100%;
}
.custom-edit-form.name-and-dict .match-box {
  display: inline-block;
  margin-bottom: 10px;
  margin-right: 10px;
}

.custom-edit-form .el-button {
  margin-left: 10px;
}
.custom-edit-form .button-new {
  margin-left: 0px;
}

.custom-edit-form .match-box-content {
  position: relative;
  display: inline-block;
}
.custom-edit-form .match_key_cascader {
  position: relative;
  display: inline-block;
}
.custom-edit-form .match_key_input {
  position: absolute;
  display: inline-block;
  top: 0;
  left: 0;
  width: 100%;
}
.custom-edit-form .rule-level-box .el-form-item__content {
  margin-left: 10px;
}
.box-card-rule {
  margin-bottom: 22px;
  border: 1px solid #409eff;
}
.box-card-rule .card-item {
  border-bottom: 1px solid #e0e3e9;
  margin-bottom: 20px;
}
.box-card-rule .card-footer {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.box-card-rule .card-item-bottom {
  text-align: right;
  margin-bottom: 20px;
}
</style>
